<template>
  <div class="container">
    <search-bar @termChange="onTermChange"></search-bar>
    <video-detail :UserSelectedVideo="selectedVideo"></video-detail>
    <video-list @videoSelect="onVideoSelect" :videos="videosDetails"></video-list>
  </div>
</template>


<script>
import axios from 'axios';
import SearchBar from './components/SearchBar';
import VideoList from './components/VideoList';
import VideoDetail from './components/VideoDetail'

export default {
  name: 'App',

  components: {
    SearchBar,
    VideoList,
    VideoDetail
  },

  data() {
    return {
      videosDetails: [],
      selectedVideo: null
    }
  },

  methods: {
    onTermChange(searchTerm) {
      const proxy = 'https://cors-anywhere.herokuapp.com/';
      axios.get(`${proxy}https://www.aparat.com/etc/api/videoBySearch/text/${searchTerm}/perpage/6`)
      .then(response => {
        //console.log(response)
        this.videosDetails = response.data.videobysearch
      })
    },

    onVideoSelect(video) {
      this.selectedVideo = video;
    }
  }


}
</script>


