<template>
  <li>
    <div @click="onVideoSelect" class="content ts">
      <img :src="singleVideoInformation.small_poster" />
      <p class="title">{{singleVideoInformation.title}}</p>
    </div>
  </li>
</template>

<script>
export default {
  name: 'VideoListItem',
  props: {
    singleVideoInformation: Object
  },
  methods: {
    onVideoSelect() {
      this.$emit('videoSelect', this.singleVideoInformation)
    }
  }
}
</script>

<style>

  li {
    width: 350px;
    height: 250px;
    padding: 0 10px 10px;
    border: 1px solid #d3d6d8;
    border-radius: 4px;
    margin: 20px 20px 0;
  }

  li p {
    padding: 10px 0;
  }
  li p:hover {
    color: orangered;
    cursor: pointer;
  }

  li img:hover {
    cursor: pointer;
  }

  .content {
    padding: 10px 0;
  }

  .title {
    font-size: 15px;
  }

</style>
