<template>
  <ul>
    <videoListItem
     v-for="video in videos"
     :singleVideoInformation="video"
     :key="video.id"
     @videoSelect="onVideoSelect"
    >
    </videoListItem>
  </ul>
</template>

<script>
import VideoListItem from './VideoListItem';

export default {
  name: 'VideoList',
  components: {
    VideoListItem
  },
  props: {
    videos: Array
  },
  methods: {
    onVideoSelect(video) {
      this.$emit('videoSelect', video)
    }
  }
}
</script>

<style>

  ul {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    list-style-type: none;
    font-size: 15px;
  }

</style>
